.TodoInsert {
  display: flex;
  // background-color: #495057;

  input {
    padding: 0.5rem;
    outline: none;
    border: none;
    border-bottom: 0.2px solid #f8f9fa;
    font-size: 1.24rem;
    line-height: 1.5;
    color: black;
    background: none;

    flex: 1;
  }

  button {
    outline: none;
    border: none;
    border-bottom: 0.2px solid #f8f9fa;
    padding-left: 1rem;
    padding-right: 1rem;
    color: black;
    font-size: 1.5rem;
    line-height: 1.5;
    // background-color: #868e96;

    display: flex;
    align-items: center;
    cursor: pointer;
    transition: background-color 0.3s ease-in;

    &:hover {
      background-color: #adb5bd;
    }
  }
}
