.TodoTemplate {
  width: 512px;
  margin: 6rem auto 0;
  border-radius: 4px;
  overflow: hidden;
  border: 1px solid #ccc;
  box-shadow: 1px 1px ccc;

  .app-title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    height: 4rem;
    font-size: 1.5rem;
    border-bottom: 1px solid #ccc;
  }

  .content {
    background-color: white;
  }
}
