.TodoListItem-virtualized {
  & + & {
    border-top: 1px solid #dee2e6;
  }
  &:nth-child(even) {
    background-color: #f8f9fa;
  }
}

.TodoListItem {
  display: flex;
  padding: 1rem;
  align-items: center;

  &:nth-child(even) {
    background-color: #f8f9fa;
  }

  .checkbox {
    display: flex;
    flex: 1;
    align-items: center;
    cursor: pointer;

    svg {
      // icon
      // cursor: pointer;
      font-size: 1.5rem;
    }

    .text {
      flex: 1;
      margin-left: 0.5rem;
    }

    // checked
    &.checked {
      svg {
        color: #22b8cf;
      }

      .text {
        color: #adb5bd;
        text-decoration: line-through;
      }
    }
  }

  .revise {
    display: flex;
    align-items: center;
    font-size: 1.1rem;
    padding-right: 0.8rem;
    cursor: pointer;

    &:hover {
      opacity: 0.5;
    }
  }

  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;

    // transition: color 0.5s;

    &:hover {
      color: #ff8787;
    }
  }

  // & + & {
  //   border-top: 1px solid #dee2e6;
  // }
}
